const safeToUpperCase = (input: string): string | undefined => {
    if (typeof input === 'string' && isNaN(Number(input))) {
        return input.toUpperCase();
    }
    return `${input}`;
}

export const NEEDS_MIGRATION = async (): Promise<boolean> => {
    try {
        return await Excel.run(async (context: Excel.RequestContext) => {
            const worksheets = context.workbook.worksheets;
            worksheets.load("items/name");
            await context.sync();

            for (const sheet of worksheets.items) {
                const usedRange = sheet.getUsedRange();
                usedRange.load(["formulas", "address"]);
                await context.sync();

                if (usedRange.formulas) {
                    for (const row of usedRange.formulas) {
                        for (const formula of row) {
                            if (typeof formula === "string") {
                                const upperCaseFormula = safeToUpperCase(formula);
                                if (
                                    (upperCaseFormula.includes("!") || upperCaseFormula.includes("[")) &&
                                    upperCaseFormula.includes("SIHOT")
                                ) {
                                    const cleanedFormula = upperCaseFormula.replace(/'.*?'!|".*?"!|[[\]]/g, "");
                                    if (cleanedFormula !== upperCaseFormula) {
                                        console.log("Match found:", formula);
                                        return true;
                                    }
                                }
                            }
                        }
                    }
                }
            }
            return false;
        });
    } catch (e: any) {
        return false;
    }
};

export const MIGRATE_ADD_ID = async (): Promise<void> => {
    await Excel.run(async (context: Excel.RequestContext) => {
        const worksheets = context.workbook.worksheets;
        worksheets.load("items/name");
        await context.sync();

        for (const sheet of worksheets.items) {
            const usedRange = sheet.getUsedRange();
            usedRange.load(["formulas", "address"]);
            await context.sync();

            if (usedRange.formulas) {
                const updatedFormulas = usedRange.formulas.map((row) =>
                    row.map((formula) => {
                        if (typeof formula === "string") {
                            formula = safeToUpperCase(formula);
                            if ((formula.includes("!") || formula.includes("[")) && formula.includes("SIHOT")) {
                                const cleanedFormula = formula.replace(/'.*?'!|".*?"!|[[\]]/g, "");
                                return cleanedFormula;
                            }
                        }
                        return formula;
                    })
                );

                usedRange.formulas = updatedFormulas;
                await context.sync();
            }
        }
    });
}

export const CALCULATE_CELL = async (context: Excel.RequestContext, range: Excel.Range, formula: string, row: number, col: number): Promise<void> => {
    try {
        if (formula) {
            formula = safeToUpperCase(formula);
            if ((formula.includes("=SIHOT") || formula.includes("=@SIHOT"))) {
                const cell = range.getCell(row, col);
                const originalFormula = formula;
                cell.formulas = [[""]];
                context.sync();
                cell.formulas = [[originalFormula]];
                context.sync();
            }
        }
    } catch (error) {
        console.error(`Calculation error at ${formula}(${row}, ${col}):`, error);
    }
}

const _CALCULATE_SHEET = async (context: Excel.RequestContext, ws: Excel.Worksheet): Promise<void> => {
    try {
        ws.load("name");
        await context.sync();

        const usedRange = ws.getUsedRange();
        context.trackedObjects.add(usedRange);
        usedRange.load("formulas");
        await context.sync();

        usedRange.formulas.forEach((row, rowIndex) => {
            row.forEach(async (formula, colIndex) => {
                CALCULATE_CELL(context, usedRange, formula, rowIndex, colIndex);
            });
        });

        context.trackedObjects.remove(usedRange);
    } catch (error) {
        console.error('Full calculation error:', error);
    }
};

/**
* Calculates the active sheet.
*/
export const CALCULATE_SHEET = async (): Promise<void> => {
    await Excel.run(async (context: Excel.RequestContext) => {
        await _CALCULATE_SHEET(context, context.workbook.worksheets.getActiveWorksheet());
    });
}

/**
* Performs a full recalculation in Excel.
*/
export const CALCULATE_FULL = async (): Promise<void> => {
    await Excel.run(async (context: Excel.RequestContext) => {
        let worksheets = context.workbook.worksheets;
        worksheets.load("items");
        await context.sync();

        let promises = worksheets.items.map(async (worksheet) => {
            _CALCULATE_SHEET(context, worksheet);
        });

        await Promise.all(promises);
    });
}
